import axios from 'axios';
import addMake from '../actions/index'
import store from "../store/index";

const addNewMake = (data) =>{
    store.dispatch( addMake(data));
}
export const getMake = () => {
  debugger

  axios.get('http://localhost:3001/api/getMake', {headers: {"Access-Control-Allow-Origin": "*"}})
  .then(res => {
    console.log(res)
    addNewMake(res.data);
  }
  ).catch((error) => { console.log('Error on getting Make : ' + error) })
};

const initialValues = () => {
  //get make
  getMake();      
};

export default initialValues;